var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',{staticClass:"d-flex flex-column",attrs:{"fluid":""}},[_c('horizontal-menu'),_c('v-row',{staticStyle:{"margin-top":"50px"},attrs:{"justify":"center","align":"start"}},[_c('v-col',{staticClass:"hidden-sm-and-down",attrs:{"cols":"6"}},[_c('v-expansion-panels',{attrs:{"accordion":""}},[_c('v-expansion-panel',[_c('v-expansion-panel-header',[_vm._v(" Программа дополнительного образования \"Читай-ка\" ")]),_c('v-expansion-panel-content',[_c('v-card',[_c('v-card-text',[_vm._v(" Новизна программы состоит в том, что она построена в соответствии с основными принципами дифференцированной теории для работы как с дошкольниками (подготовка к обучению грамоте, развитие всех психических процессов), так и со школьниками, имею­щими нарушения письменной речи и проблемы с чтением. Для детей различного возраста (от 5—9 лет) и разного интеллектуального уровня развития — от нормы до задержки психического развития и до олигофрении, потому что все строится на методе дидактической (обучающей и развивающей) игры. В основу данной программы положены такие дидактические принципы, как переход от простого к сложному, системность и концентричность при изучении материала. Который подобран с учетом актуальности той или иной темы для детей старшего и подготовительного к школе возраста. ")]),_c('v-card-actions',[_c('v-btn',{attrs:{"dark":"","color":"#16a085","href":"https://multiurok.ru/files/proghramma-dopolnitiel-nogho-obrazovaniia-chitai-k.html"}},[_vm._v(" Узнать больше... ")])],1)],1)],1)],1),_c('v-expansion-panel',[_c('v-expansion-panel-header',[_vm._v(" Программа по формированию беглого и динамического чтения у учащихся начальных классов ")]),_c('v-expansion-panel-content',[_c('v-card',[_c('v-card-text',[_vm._v(" Научить детей правильному, беглому, осознанному, выразительному чтению – одна из задач начального образования. И эта задача чрезвычайно актуальна, так как чтение играет огромную роль в образовании, воспитании и развитии человека. Чтение – это окошко, через которое дети видят и познают мир и самого себя. Чтение – это и то, чему обучают младших школьников, посредствам чего их воспитывают и развивают. Умения и навыки чтения формируются не только как важнейший вид речевой и умственной деятельности, но и как сложный комплекс умений и навыков, имеющий обучающий характер, используемый учениками при изучении всех учебных предметов, во всех случаях внеклассной и внешкольной жизни. Следовательно, необходима, систематическая, целенаправленная работа над развитием и совершенствованием навыков беглого, осознанного чтения от класса к классу. ")]),_c('v-card-actions',[_c('v-btn',{attrs:{"dark":"","color":"#16a085","href":"https://nsportal.ru/nachalnaya-shkola/chtenie/2016/01/20/rabochaya-programma-po-razvitiyu-tehniki-chteniya"}},[_vm._v(" Узнать больше... ")])],1)],1)],1)],1),_c('v-expansion-panel',[_c('v-expansion-panel-header',[_vm._v(" Хорошо читаем - отлично учимся (Система упражнений для формирования устойчивого навыка чтения у младших школьников) ")]),_c('v-expansion-panel-content',[_c('v-card',[_c('v-card-text',[_vm._v(" Уверенное владение навыком чтения является одним из основных условий успешности детей в учении. Однако в настоящий момент навык чтения у школьников вызывают серьёзную тревогу у педагогов и родителей. Подавляющее большинство трудностей, которые наблюдаются у детей во время обучения в школе, связаны с тем, что они не могут самостоятельно получать информацию из книг и учебников. Современные дети не просто не любят, но и не умеют читать, а чтение – это не только тот предмет, которым надо успешно овладеть ученику, но и тот, посредством которого он будет осваивать другие дисциплины, познавать богатство окружающего мира и человеческих отношений, формировать в себе собственное отношение к действительности. Чтение – это труд. Наша задача - сделать его более производительным, продуктивным. Навык беглого, осознанного и выразительного чте